.App {
    text-align: center;
}

.box {
    position: absolute;
    cursor: move;
    color: black;
    background-color: white;
    max-width: 215px;
    overflow-wrap: break-word;
    border-radius: 5px;
    padding: 1em;
    margin: auto;
    user-select: none;
}

.body {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    color: white;
}

#new-item {
    user-select: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

input {
    border-radius: 5px 5px 5px 5px;
    color: black;
    font-size: 1.3em;
    background-color: transparent;
    padding: 0.5em;
}

input:focus {
    outline: none;
}

button {
    cursor: pointer;
    background-color: gray;
    font-size: 1em;
    padding: 0.7em;
    border-radius: 5px 5px 5px 5px;
}

button:hover {
    background-color: rgb(173, 162, 162);
}

#delete {
    font-size: 0.6em;
    position: fixed;
    top: 0;
    right: 0;
    background-color: transparent;
    border: none;
}

#delete:hover {
    color: rgb(255, 55, 55);
}