@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Poppins:wght@300&display=swap');
.App {
    text-align: center;
}

.title {
    font-size: 3vh;
    font-family: Poppins;
    padding-top: 1.2vh;
    padding-bottom: 1.2vh;
    height: 6vh;
    background-color: rgb(236, 233, 178);
}

.App-logo {
    height: 20vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.body {
    background: white;
    fill: black;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.h1 {
    padding-top: 3vh;
    padding-bottom: 2vh;
    font-size: 5vh;
    font-family: Poppins;
}

.h2 {
    font-size: 2vh;
    padding-bottom: 2vh;
    font-family: Poppins;
}

.MathJax {
    font-size: 10pt;
    padding: 1vh;
}

.Button {
    font-size: 2vh;
    font-family: Poppins;
}

.chart {
    padding: 3vh;
}

.App-link {
    color: #61dafb;
}

.Paragraph {
    padding-top: 1vh;
    padding-bottom: 1vh;
    text-align: left;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.Table {
    font-size: 1.2vh;
    font-family: Lato;
}