@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.h2 {
    font-size: 15pt;
}
.App {
    text-align: center;
}

.title {
    font-size: 3vh;
    font-family: Poppins;
    padding-top: 1.2vh;
    padding-bottom: 1.2vh;
    height: 6vh;
    background-color: rgb(236, 233, 178);
}

.App-logo {
    height: 20vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.body {
    background: white;
    fill: black;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.h1 {
    padding-top: 3vh;
    padding-bottom: 2vh;
    font-size: 5vh;
    font-family: Poppins;
}

.h2 {
    font-size: 2vh;
    padding-bottom: 2vh;
    font-family: Poppins;
}

.MathJax {
    font-size: 10pt;
    padding: 1vh;
}

.Button {
    font-size: 2vh;
    font-family: Poppins;
}

.chart {
    padding: 3vh;
}

.App-link {
    color: #61dafb;
}

.Paragraph {
    padding-top: 1vh;
    padding-bottom: 1vh;
    text-align: left;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.Table {
    font-size: 1.2vh;
    font-family: Lato;
}
.App {
    text-align: center;
}

.box {
    position: absolute;
    cursor: move;
    color: black;
    background-color: white;
    max-width: 215px;
    overflow-wrap: break-word;
    border-radius: 5px;
    padding: 1em;
    margin: auto;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.body {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    color: white;
}

#new-item {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

input {
    border-radius: 5px 5px 5px 5px;
    color: black;
    font-size: 1.3em;
    background-color: transparent;
    padding: 0.5em;
}

input:focus {
    outline: none;
}

button {
    cursor: pointer;
    background-color: gray;
    font-size: 1em;
    padding: 0.7em;
    border-radius: 5px 5px 5px 5px;
}

button:hover {
    background-color: rgb(173, 162, 162);
}

#delete {
    font-size: 0.6em;
    position: fixed;
    top: 0;
    right: 0;
    background-color: transparent;
    border: none;
}

#delete:hover {
    color: rgb(255, 55, 55);
}
